.badge-tag {
    vertical-align: middle;
    font-size: 1.2em;
    margin-block-end: 5px;
    margin-inline-end: 5px;
    background-color: black;
    color: red;
    border: red thin solid;
}
.active {
    color: black;
    background-color: red;
}

.exclude::before {
    font-family: "Font Awesome 5 Free";
    font-weight: 800;
    content: "\f05e";
    display: inline-block;
    padding-right: 5px;
}